import React, { Component } from "react";
import axios from "axios";
import { Host } from  "../../../Host";
import {
    Form,
    Input,
    Divider,
    Tooltip,
    Icon,
    Cascader,
    Select,
    Row,
    Col,
    Card,
    Modal,
    Drawer,
    Checkbox,
    Button,
    Radio
} from "antd";

class FormAccountSetting extends Component {
 constructor(props) {
    super(props);
    this.state = {
     User_Name:'',
     User_Email:'',
     Account_type:'',
     profile_up_data :JSON.parse(sessionStorage.getItem("session_data")),
    }
 }

 handleSubmit = (e) => {
    const profile_data = JSON.parse(sessionStorage.getItem("session_data"));
    e.preventDefault();
     const first_nam = e.target.fname.value;
     const last_nam = e.target.lname.value;
     const officePhone = e.target.officePhone.value;
     const cellPhone = e.target.cellPhone.value;
     const homePhone = e.target.homePhone.value;
     const address = e.target.address_1.value +' ' + e.target.address_2.value +' ' + e.target.city.value
                      +' '+ e.target.state.value +'-' +e.target.zipCode.value;
     const req_data =   {'username':profile_data["username"], 'email':profile_data["email"],'first_nam':first_nam,
                        'last_nam':last_nam, 'office_number' : officePhone, 'cell_number':cellPhone,
                         'home_number':homePhone, 'address':address, 'flog_value':'profileUpdate'}
     this.ProfileUpdateAPI(req_data);
};
 async ProfileUpdateAPI(req_data){

    const headers  = new Headers()
    headers.append('Content-Type', 'application/json');
    const options = {
    method : 'POST',
    headers,
    body:JSON.stringify(req_data),
    }
    try{
     const promise = await axios.post(Host.loginURL+"/signup_api", options);
     const status = promise.status;
     if(status==200)
    {
      const data = promise.data.data;
      console.log(data)
      const p = JSON.parse(sessionStorage.getItem("session_data"));
      const session_data = promise.data.data;
      sessionStorage.setItem("session_data",JSON.stringify(session_data));
      this.setState({profile_up_data:data})
      await window.location.reload();
     }

  }
  catch(err){this.setState({msg: "Profile data update"});}
//  this.setState({msg: "Username & Password is Invalid"});
}

    render () {

        const formItemLayout = {
            labelCol: {
            xs: { span: 24 },
            sm: { span: 8 },
            },
            wrapperCol: {
            xs: { span: 24 },
            sm: { span: 16 },
            },
        };
        const tailFormItemLayout = {
            wrapperCol: {
            xs: {
                span: 24,
                offset: 0,
            },
            sm: {
                span: 16,
                offset: 8,
            },
            },
        };
        const { getFieldDecorator } = this.props.form;

        return(
            <Form {...formItemLayout}  onSubmit={this.handleSubmit}>
                
                <Row>
                    <Col span={16} offset={8}>
                        <h6 className="mb-3">Contact Information</h6>
                    </Col>
                </Row>
                <Form.Item label="First Name">
                    {getFieldDecorator('fname' ,{initialValue:this.state.profile_up_data['first_name']})
                    (<Input name="first_name" />)}
                </Form.Item>
                <Form.Item label="Last Name">
                    {getFieldDecorator('lname',{initialValue:this.state.profile_up_data['last_name']})(<Input />)}
                </Form.Item>
                <Form.Item label="Office Phone">
                    {getFieldDecorator('officePhone',{initialValue:this.state.profile_up_data['office_number']})(<Input />)}
                </Form.Item>
                <Form.Item label="Cell Phone">
                    {getFieldDecorator('cellPhone',{initialValue:this.state.profile_up_data['cell_number']})(<Input />)}
                </Form.Item>
                <Form.Item label="Home Phone">
                    {getFieldDecorator('homePhone',{initialValue:this.state.profile_up_data['home_number']})(<Input />)}

                </Form.Item>


                <Row>
                    <Col span={16} offset={8}>
                        <h6 className="mb-3">Address</h6>
                    </Col>
                </Row>
                <Form.Item label="Address 1">
                    {getFieldDecorator('address_1',{initialValue:this.state.profile_up_data['address']})(<Input />)}
                </Form.Item>
                <Form.Item label="Address 2">
                    {getFieldDecorator('address_2',{initialValue:this.state.profile_up_data['address']})(<Input />)}
                </Form.Item>
                <Form.Item label="City">
                    {getFieldDecorator('city',{initialValue:this.state.profile_up_data['address']})(<Input />)}
                </Form.Item>
                <Form.Item label="State">
                    {getFieldDecorator('state',{initialValue:this.state.profile_up_data['address']})(<Input />)}
                </Form.Item>
                <Form.Item label="Zip Code">
                    {getFieldDecorator('zipCode',{initialValue:this.state.profile_up_data['address']})(<Input />)}
                </Form.Item>
                <Form.Item {...tailFormItemLayout}>
                    <Button type="primary" htmlType="submit">
                        Update
                    </Button>
                </Form.Item>
            </Form>
        );
    }
}
const WrappedAccountForm = Form.create()(FormAccountSetting);
export default WrappedAccountForm;